import React, { Component, Fragment } from "react";
import styled from "styled-components";

const ContainerWrap = styled.div`
  position: fixed;
  top: 0;
  left: 0;

  width: 100%;
  height: 100vh;
  -webkit-perspective: 150px;
  perspective: 150px;

  .womanItem {
    width: 100vw;
    outline: 1px solid ${props => props.theme.green};
    display: flex;
    justify-content: center;
    align-items: center;

    h1 {
      color: ${props => props.theme.green};
      text-transform: uppercase;
    }
  }
`;

const ContainerTop = styled.div`
  height: calc(100vh / 3);
  overflow: hidden;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 2px solid red;
  .womenWrapper {
    /* scroll position */
    transform: translate3d(0px, -1710px, 0px);
  }
`;

const ContainerMiddle = styled.div`
  height: calc(100vh / 3);
  overflow: hidden;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 2px solid blue;

  .womenWrapper {
    margin-top: calc(-100vh / 3);
    /* scroll position */
    transform: translate3d(0px, -1710px, 0px);
  }
`;

const ContainerBottom = styled.div`
  height: calc(100vh / 3);
  overflow: hidden;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: 2px solid yellow;

  .womenWrapper {
    /* scroll position */
    transform: translate3d(0px, -1710px, 0px);
    margin-top: calc((-100vh / 3) * 2);
  }
`;

const calcX = x => {
  if (typeof window === "undefined") {
    return 0;
  } else {
    // measure centerpoint
    const winWidth = window.innerWidth;
    const center = winWidth / 2;
    const distFromCenter = x - center;
    const decimal = distFromCenter / center;
  }
};

export const WomenList = ({ women, x, y }) => {
  if (women) {
    const allWomen = women.map(woman => {
      return (
        <div className="womanItem" key={woman.id}>
          <h1>{woman.name}</h1>
        </div>
      );
    });

    const containers = (
      <ContainerWrap>
        <ContainerTop>
          <div className="womenWrapper"> {allWomen}</div>
        </ContainerTop>
        <ContainerMiddle>
          <div className="womenWrapper"> {allWomen}</div>
        </ContainerMiddle>
        <ContainerBottom>
          <div className="womenWrapper"> {allWomen}</div>
        </ContainerBottom>
      </ContainerWrap>
    );

    return containers;
  } else return null;
};
